import ls from '@/libs/localstorage-lib';

/**
 * @description Essa lib deve ser utilizada para futuras
 * alteraes referentes aos dados do github do usurio
 * logado. Alm do token, poder ser armazenado dados
 * sobre o profile do usurio e outras informaes at
 * o momento em que se tornar necessrio utilizar um
 * banco de dados de fato.
 * @author Luiz Alberto (onluiz)
 */
const authLib = (() => {
  const setToken = token => ls.addItem('token', token);
  return {
    setToken,
  };
})();

export default authLib;



